const plugin = new window.VConsole.VConsolePlugin("plugin", "Reload");

// 打印必要信息
plugin.on("init", function () {
  console.info("userAgent:", window.navigator.userAgent);
  console.info("href:", window.location.href);

  let search = window.location.search;
  if (search) {
    search = search.replace("?", "");
    search = search.split("&");
    console.info("search:");
    for (let i = 0; i < search.length; i++) {
      console.info(search[i]);
    }
  }
});
plugin.on("addTool", function (callback) {
  const reload = {
    name: "Reload",
    global: true,
    onClick: function () {
      window.location.reload();
    },
  };
  const clearStorage = {
    name: "Clear Storage",
    global: true,
    onClick: function () {
      window.localStorage.clear();
    },
  };
  callback([reload, clearStorage]);
});
const vConsole = new window.VConsole();
vConsole.addPlugin(plugin);
